import { Component, OnInit } from '@angular/core';
import { AuthService } from '../app/_services/auth.service';
import { Router } from '@angular/router';
import { OrgUser } from './_models/orgUser';
import { AdminService } from './_services/admin.service';
import { ProjectService } from './_services/project.service';
import { analyzeAndValidateNgModules } from '@angular/compiler';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Frontend';
  private _router: Router;
  usersOrganizations: any = [];
  isSystemAdmin: boolean = false;
  valueHolder: any;
  loggedInUser: any = {};

  constructor(public _authService: AuthService, private router: Router, private _adminservice: AdminService, private _projectservice: ProjectService) 
  { 
    this._router = router;
  }

  ngOnInit() {
    this.adminCheck();
    this.getLoggedInUser();
  }

  goToCreateNewActivity() {
    this._router.navigateByUrl('createactivity');
  }

  private signOut() 
  {
    localStorage.clear()
    this._router.navigateByUrl('');
  }

  getLoggedInUser() {
    this._authService.getLoggedInUser().subscribe(data => this.loggedInUser = data);
  }

  showOrganizations()
  {
    this._adminservice.getUsersOrganizationsToken()
    .subscribe(data => this.usersOrganizations = data);
  }

  changeOrg(orgId: number) 
  {
    this._authService.changeOrg(orgId).subscribe();
    location.reload();
  }

  refreshTrello()
  {
    this._projectservice.updateTrelloData().subscribe();
  }

  async adminCheck()
  {
    this._authService.isSystemAdmin().subscribe(data => this.valueHolder = data);
    await this.delay(200);
    if(this.valueHolder == 1)
    {
      this.isSystemAdmin = true;
    }
    else
    {
      this.isSystemAdmin = false;
    }
  }

  private delay(ms: number)
    {
      return new Promise(resolve => setTimeout(resolve, ms));
    }

}
