import { Component, OnInit } from '@angular/core';
import { ProjectService } from '../../_services/project.service';
import { SwalPartialTargets } from '@sweetalert2/ngx-sweetalert2';

@Component({
  selector: 'app-userProjects',
  templateUrl: './userProjects.component.html',
  styleUrls: ['./userProjects.component.css']
})
export class UserProjectsComponent implements OnInit {

  myProjects: any = [];

  constructor(private _projectService: ProjectService, public readonly swalTargets: SwalPartialTargets) { }

  ngOnInit() {
   this.getUserProjects() 
  }

getUserProjects(){
 this._projectService.getAllProjectsForUser().subscribe(data => this.myProjects = data);
}

}
