import { Component, OnInit } from '@angular/core';
import { BoardService } from '../../_services/board.service';
import { AdminService } from '../../_services/admin.service';
import { DragDropModule, moveItemInArray, CdkDragDrop, transferArrayItem } from '@angular/cdk/drag-drop';
import { ProjectService } from '../../_services/project.service';
import { ActivatedRoute} from '@angular/router';


@Component({
  selector: 'app-projectAssigner',
  templateUrl: './projectAssigner.component.html',
  styleUrls: ['./projectAssigner.component.css']
})
export class ProjectAssignerComponent implements OnInit {

  public Boards = [];
  projects: any = [];
  users: any = [];
  usersToProject: any = [];
  projectId: any;
  

  constructor(private _boardService: BoardService, private _adminService: AdminService, private _projectService: ProjectService, private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    // this.getTrelloBoards();
    this.getAllProjects();
    // this.getAllUsers();
   
  }

  // getTrelloBoards() {
  //   this._boardService.getTrelloBoards()
  //   .subscribe(data => this.Boards = data);
  // }

  getAllProjects() {
    this._adminService.getAllProjects()
      .subscribe(data => this.projects = data);
  }

  // getAllUsers() {
  //   this._adminService.getAllUsers()
  //     .subscribe(data => this.users = data);
  //     console.log(this.users);
  // }






  drop(event: CdkDragDrop<string[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    } else {
      transferArrayItem(event.previousContainer.data,
                        event.container.data,
                        event.previousIndex,
                        event.currentIndex);
    }
  }

  showProjectMembers(id: any) {
    this.projectId = id;
   //this.usersToProject = [];
   this._projectService.getAllCurrentProjectUsers(id).subscribe(data => this.usersToProject = data);
   this._projectService.getAllProjectUsers(id).subscribe(data => this.users = data);
  }

  addMembersToProject() {
    if (this.usersToProject.length < 1 ) {
      console.log("this error");
    } 
    else {
      this._projectService.addUsersToProject(this.projectId, this.usersToProject).subscribe();
      //this.usersToProject = [];
    }
  }


}
