import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/_services/admin.service';
import { User } from 'src/app/_models/user';
import { moveItemInArray, CdkDragDrop, transferArrayItem } from '@angular/cdk/drag-drop';
import { project } from 'src/app/_models/project';
import { ProjectService } from 'src/app/_services/project.service';
import { CustomerService } from 'src/app/_services/customer.service';


@Component({
  selector: 'app-createProject',
  templateUrl: './createProject.component.html',
  styleUrls: ['./createProject.component.css']
})
export class CreateProjectComponent implements OnInit {
  customers: any = {};
  contacts: any = {};
  project: any= {};

  
  // priorities: any = [{ value: 'low', display: 'Low'},
  // { value: 'medium', display: 'Medium'},
  // { value: 'high', display: 'High'},];

  selectedValuePriority: string; 
  users: any = {};
  selectedUsers: any[];


  constructor(private _customerService: CustomerService,private adminService: AdminService, private projectService: ProjectService) { }

  ngOnInit() {

    this.getUsers();
    this.getAllCustomers();
  
  }


getAllCustomers() {
    this._customerService.getAllCustomersForOrganization().subscribe(data => this.customers = data);
  }
  
  getAllContactsForSelectedCustomer(customerId) {
    this._customerService.getContactpersonsByCustomerId(customerId).subscribe(data => this.contacts = data);
  }


test(){
  console.log(this.selectedValuePriority);
  console.log(this.project);
}

getUsers(){
  // this.adminService.getAllUsers().subscribe((users: User[]) => {
  //   this.users = users;
  // }, error => {
  //   console.log(error);
  // });
  this.adminService.getAllUsers().subscribe(data => this.users = data);
}

drop(event: CdkDragDrop<string[]>) {
  if (event.previousContainer === event.container) {
    moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
  } else {
    transferArrayItem(event.previousContainer.data,
                      event.container.data,
                      event.previousIndex,
                      event.currentIndex);
  }
}


createProject(){

this.projectService.createProject(project).subscribe();
}

}

