import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ProjectsComponent } from './project/projects/projects.component';
import { LoginComponent } from './login/login.component';
import { ProjectAssignerComponent } from './project/projectAssigner/projectAssigner.component';
import { RegisterComponent } from './admin/register/register.component';
import { AdminPanelComponent } from './admin/admin-panel/admin-panel.component';
import { RoleManagementComponent } from './admin/role-management/role-management.component';
import { OrganizationManagementComponent } from './admin/organization-management/organization-management.component';
import { UserProjectsComponent } from './project/userProjects/userProjects.component';
import { PmsAdminPanelComponent } from './project/pms-admin-panel/pms-admin-panel.component';
import { ProjectComponent }  from './project/project/project.component';
import { CustomerComponent } from './customer/customer.component';
import { CustomerListComponent } from './customer-list/customer-list.component';
import { CreateActivityComponent } from './activity/createActivity/createActivity.component';
import { UserManagementComponent } from './admin/user-management/user-management.component';


const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'users', component: UserManagementComponent },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'adminpanel', component: AdminPanelComponent},  // BORT
  { path: 'rolemanagement', component: RoleManagementComponent},
  { path: 'projectAssigner', component: ProjectAssignerComponent }, // BORT
  { path: "organizationmanagement", component: OrganizationManagementComponent },
  { path: 'project/:id', component: ProjectComponent},
  { path: 'projects', component: ProjectsComponent},
  { path: 'userProjects', component: UserProjectsComponent },   // BORT
  { path: 'pmsAdminPanel', component: PmsAdminPanelComponent},  // BORT
  { path: 'customer/:id', component: CustomerComponent},
  { path: 'customerlist', component: CustomerListComponent},
  { path: 'createactivity', component: CreateActivityComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
