import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { IBoard } from 'src/board';
import { map } from 'rxjs/operators';
import { pipe } from '@angular/core/src/render3';
import { Options } from 'selenium-webdriver/ie';
import { BoardService } from './board.service';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {

  private url = 'http://localhost:5000/api/admin/projectmembers/';
  private BaseUrl = 'http://localhost:5000/api/project/';

constructor(public http: HttpClient) { }

createProject(project: any){
  return this.http.post('http://localhost:5000/api/project/createproject', project)
}


getAllProjectUsers(id: any) {     //! TOKIGT NAMN DÅ DEN HÄMTAR MOTSATSEN
  return this.http.get(this.url + id);
}

getAllCurrentProjectUsers(id: any) {
  return this.http.get('http://localhost:5000/api/admin/projectmembersinproject/' + id);
}

addUsersToProject(id: any, users: any) {

  return this.http.post('http://localhost:5000/api/admin/createProjectUsers/' + id , users);
}

getAllProjectsForUser() {
return this.http.get('http://localhost:5000/api/users/userprojects');
}

updateTrelloData() {
return this.http.get('http://localhost:5000/api/trello/updatetrellodata');
}

getAllProjects(){
  return this.http.get(this.url + "projects");
}
getUserTasks(projectId: any, listId: any){
  return this.http.get('http://localhost:5000/api/project/getUserTasks/' + projectId+ "/" +listId );
}

getProjectValues(id: any) {
  return this.http.get(this.BaseUrl + 'projectvalues/' + id)
}

getBoardLists(Id: any){
  return this.http.get('http://localhost:5000/api/project/projectLists/' + Id);
}

getAvaliableBoards() {
  return this.http.get(this.BaseUrl + 'availableboards');
}

changeTrelloBoardConnection(values: any) {
  return this.http.put(this.BaseUrl + 'changetrelloboard', values);
}

removeTrelloBoardConnection(id: any) {
  return this.http.put(this.BaseUrl + 'removetrelloconnection',  id);
}

}

