import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { IBoard } from 'src/board';
import { map } from 'rxjs/operators';
import { pipe } from '@angular/core/src/render3';
import { Options } from 'selenium-webdriver/ie';

@Injectable({
  providedIn: 'root'
})

export class BoardService {

  private _getUrl = 'https://api.trello.com/1/members/me/boards?key=334394719e70cc9d2b048285ec73053d&token=8c134b38cfab2ae7c699cb4bfa3c8c411d1834f2efaee6f363b837e486848073';
    // API för att returnera en array av boards


  constructor(public http: HttpClient) { }

  // * Trello Get All Boards
  getTrelloBoards(): Observable<IBoard[]> {
    return this.http.get<IBoard[]>(this._getUrl);
  }

  postTrelloBoard(boardName: string) {
    return this.http.post(boardName, null).pipe(
      map((response: any) => {
        const name = response;
      })
    );
  }

  archiveTrelloBoard(boardId: string) {
    return this.http.put(boardId, null);
  }

  deleteTrelloBoard(boardId: string) { // !Används ej än
    return this.http.delete(boardId);
  }

  addMemberToTrelloBoard(memberId: string) {
    return this.http.put('https://api.trello.com/1/boards/' + memberId + '?type=normal&allowBillableGuest=false', null);
     // Testa och kolla om det funkar senare.
  }
}
